header#MainMenu {
  width: 100%;
  max-width: 1100px;
  height: 68px;
  margin: 0 auto;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

header#MainMenu img {
  height: 64px;
}

header#MainMenu ul {
  display: flex;
  flex-direction: row;
  list-style: none;
}
header#MainMenu ul li {
  margin: 0px 6px;
}
header#MainMenu ul li a {
  padding: 8px;
  text-decoration: none;
  color: white;
}